@font-face {
  font-family: 'header';
  src: url(./font/Mont.otf);
}

@font-face {
  font-family: 'body';
  src: url(./font/Montserrat-Regular.ttf);
}

*{
  margin: 0;
  padding: 0;
}

body{
  width: calc(100vw - (100vw - 100%));
}

.App{
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.Header{
  font-family: 'header';
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  
}

.Header h1{
  color: whitesmoke;
  padding: 0.25rem;
  font-size: 2rem;
  font-weight: bold;
}

.Header h2{
  color: #0033A0;
  font-size: 4rem;
}

.desc{
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 1rem;
  width: 85%;
}

.title{
  display: flex;
  align-items: center;
  justify-content: center;
  width: 85%;
}

.title-background{
  background-color: #0033A0;
  width: 100%;
  display: flex;
  justify-content: center;

}

.title h1{
  width: 100%;
}

.B-Form{
  display: flex;
  flex-direction: row;
  justify-content: center;
  width: 100%;
}

.Form{
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  width:85%;
}



form{
  width: fit-content;
  display: flex;
  flex-direction: column;
}


select{
  outline: none;
  -webkit-tap-highlight-color: rgba(0,0,0,0);
  font-family: 'body';
  font-size: 1rem;
  width: 30rem;
  margin: 1rem;
  border: none;
  border-bottom: 3px solid #0033A0;
  padding: 0.5rem;
}

form button{
  -webkit-tap-highlight-color: rgba(0,0,0,0);
  border: none;
  outline: none;
  background-color: #0033A0;
  font-family: 'header';
  font-size: 1.5rem;
  font-weight: normal;
  margin: 1rem;
  padding: 1rem;
  width: 30rem;
  cursor: pointer;
  border: solid 3px transparent;
  transition: all 500ms;
  background-color: whitesmoke;
  color: #0033A0;
  border: solid 3px #0033A0;
}

form button:hover{
  background-color: #0033A0;
  color: whitesmoke;
}

.List{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  width: 100%;
}

.list-drinks{
  width:85%;
  display: flex;
  flex-wrap: wrap;
}

.drink{
  height:25rem;
  width: 33%;
  background-size: cover;
  background-repeat: no-repeat;
  position: relative;
  overflow: hidden;
}

.name-drink{
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  width: 100%;
  height: 100%;
  font-family: 'header';
  font-size: 2.5rem;
  text-align: center;
  background-color: #1c1c1c6e;
  transition: all 300ms;
}

.button-ing{
  background-color: #0033A0;
  color: white;
  font-family: 'body';
  width: 100%;
  border: none;
  height: fit-content;
  position: absolute;
  top: 100%;
  transition: all 500ms;
  font-size: 1.1rem;
  font-weight: lighter;
  cursor: pointer;
  line-height: 3rem;
  padding-bottom: 2rem;
}

.button-ing span{
  background-color: white;
  color: #0033A0;
  padding: 0.5rem;
  margin: 0;
  font-size: 1.5rem;

}

.drink:hover .button-ing{
  top: 40%;
}

.drink:hover .name-drink{
  background-color: #1c1c1c00;
  position: absolute;
  top: 100%;

}

.or{
  padding-left: 1rem;
  color: #0033A0;
  font-family: 'header';
  font-size: 2rem;
}

.animation-water{
  background: #0033A0;
  width: 59%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  overflow: hidden;
}

.selected{
  color:white;
  font-family: 'body';
  font-size: 3rem;
  font-weight: bold;
  width: 50%;
  height: 40%;
  text-align: center;
  transition: all 500ms;
}

.waves{
  background: url("./img/waves.png");
  width: 100%;
  height: 100%;
  background-size: contain;
  position: absolute;
  top: 20%;
  animation: roll 100s linear infinite; 
}

@keyframes roll {
  from {
    background-position: 0 0;
  }

  to {
    background-position: -25000px 0;
  }
}